import React, { useContext, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';
import { AuthContext } from '../auth/AuthContext';

export const LoginPage = () => {
    const {login} = useContext(AuthContext);
    const [form, setForm] = useState({
        email: 'test@email.com',
        password: '123456',
        rememberme: false
    });

    useEffect(() => {
        const remembermeEmail = localStorage.getItem('email');

        if(remembermeEmail){
            //se escribe de esta manea para evitar el warning de la dependencia faltante que es form
            setForm((form) => ({
                ...form,
                rememberme: true,
                email: remembermeEmail,
            }));
        }
    }, []);

    const onChange = ({target}) => {
        const {name, value} = target;
        //console.log(name, value);
        setForm({
            ...form,
            //las llaves cuadradas sirven para que use la propiedad name y no cree un nuevo campo en el objeto
            [name]: value
        });
    };

    const toggleCheck = () => {
        setForm({
            ...form,
            rememberme: !form.rememberme,
        });
    };

    const onSubmit = async (event) => {
        event.preventDefault();
        //console.log(form);

        if(form.rememberme){
            localStorage.setItem('email', form.email);
        }else{
            localStorage.removeItem('email');
        }

        const { email, password } = form;
        const ok = await login(email, password);

        if(!ok){
            Swal.fire('Error', 'Verifique el usuario y contrase침a', 'error');
        }
    };

    const allOK = () => {
        return (form.email.length >0 && form.password > 0) ? true : false;
    };

    return (
        <form className="login100-form validate-form flex-sb flex-w" onSubmit={onSubmit}>
            <span className="login100-form-title mb-3">
                Chat - Ingreso
			</span>

            <div className="wrap-input100 validate-input mb-3">
                <input className="input100" type="email" name="email" placeholder="Email" value={form.email} onChange={onChange} />
                <span className="focus-input100"></span>
            </div>


            <div className="wrap-input100 validate-input mb-3">
                <input className="input100" type="password" name="password" placeholder="Password" value={form.password} onChange={onChange} />
                <span className="focus-input100"></span>
            </div>

            <div className="row mb-3">
                <div 
                    className="col"
                    //se define aca porque es por el template que no permite hacer click sobre el check
                    onClick={() => toggleCheck()}
                >
                    <input className="input-checkbox100" id="ckb1" type="checkbox" name="rememberme" checked={form.rememberme} readOnly />
                    <label className="label-checkbox100">
                        Recordarme
					</label>
                </div>

                <div className="col text-right">
                    <Link to="/auth/register" className="txt1">
                        Nueva cuenta?
					</Link>
                </div>
            </div>

            <div className="container-login100-form-btn m-t-17">
                <button
                    type='submit'
                    className="login100-form-btn"
                    disabled={!allOK()}
                >
                    Ingresar
				</button>
            </div>

        </form>
    );
};